// text Carousel
const StyelsPanel = styled(Box)`
    .activeStatus{
      width: 15px;
      height:15px;
      background-color: #fff;
    }
`

const StyelsH1 = styled(Box)`

  animation: fadeInAnimation ease 3s; 
  animation-iteration-count: 1; /*设置动画播放次数*/
  animation-fill-mode: forwards; /*设置样式以在动画不播放时应用元素。forward是设置动画结束后，使用元素的结束属性值*/
  @keyframes fadeInAnimation { 
    0% { 
        opacity: 0; 
    } 
    100% { 
        opacity: 1; 
    };
  }

  /* @keyframes fadenum{
    100%{transform:translateX(-50px);opacity:0;}
  } */

  /* @keyframes fadeInAnimation { 
    0% { 
        opacity: 1; 
    } 
    100%{
       transform:translateY(5px);
       opacity:0;
    }
  } */

   
`

const Lunbo=styled(Box)`
  width: 10px;
  height:10px;
  background-color: red;
`

const TextCarousel=()=>{
  const [words,setwords]= useState(['Hello', 'World', 'Foo', 'Bar']);
  const [text, setText] = useState<string>(words[0]);
  const index = useRef(0);
  const interval = useRef<any>();

  const startInter=useCallback(()=>{
    interval.current = setInterval(() => {
      setText("");
      index.current+=1;
      if(index.current===words.length){
        index.current=0;
        setText(words[0]);
        console.log("index.current",0)
      }else{
        setText(words[index.current]);
        console.log("index.current",index.current)
      }
  }, 2000);
  },[index,words])

  useEffect(() => {
    startInter();
    return () => clearInterval(interval.current);
  }, [startInter]);
  
  const handClick=(idx:number)=>{
        setText(words[idx]);
        index.current=idx;
        // 点击清楚定时
        clearInterval(interval.current);
        startInter();
  }
  return <>

        <StyelsPanel>
            {words.map((t,idx)=>{
              return <Lunbo onClick={()=>{handClick(idx)}} className={text===t?'activeStatus':''} ml={2} />
            })}
        </StyelsPanel>
        {
          text?<StyelsH1>{text}</StyelsH1>:''
        }
        
  </>;
}